import { Line } from 'react-chartjs-2';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { playerGoalsBySeason, seasons } from '../data/mlsData';

export default function LineChart() {
  const { t } = useTranslation();
  const [selectedPlayer, setSelectedPlayer] = useState('Denis Bouanga');

  const data = {
    labels: seasons,
    datasets: [{
      label: `${selectedPlayer} - ${t('goalsPerSeason')}`,
      data: playerGoalsBySeason[selectedPlayer],
      borderColor: 'rgba(255, 99, 132, 0.6)',
      fill: false
    }]
  };

  return (
    <div>
      <label>{t('selectPlayer')}</label>
      <select onChange={e => setSelectedPlayer(e.target.value)}>
        {Object.keys(playerGoalsBySeason).map(player => (
          <option key={player}>{player}</option>
        ))}
      </select>
      <Line data={data} />
    </div>
  );
}
